









































.modal {
    overflow-x: hidden;
    overflow-y: auto;
    position:   fixed;
    top:        0;
    right:      0;
    bottom:     0;
    left:       0;
    z-index:    9;
    &__backdrop {
        background-color: rgba(0, 0, 0, 0.3);
        position:         fixed;
        top:              0;
        right:            0;
        bottom:           0;
        left:             0;
        z-index:          1;
    }
    &__dialog {
        background-color: #FFFFFF;
        position:         relative;
        width:            600px;
        margin:           50px auto;
        display:          flex;
        flex-direction:   column;
        border-radius:    5px;
        z-index:          2;
        @media screen and (max-width: 992px) {
            width: 90%;
        }
    }
    &__close {
        width:       30px;
        height:      30px;
        display:     flex;
        flex-flow:   row nowrap;
        align-items: center;
        &__svg {
            height: auto;
            width:  100%;
        }
    }
    &__header {
        padding:         20px 20px 10px;
        display:         flex;
        align-items:     flex-start;
        justify-content: space-between;
    }
    &__body {
        padding:        10px 20px 10px;
        overflow:       auto;
        display:        flex;
        flex-direction: column;
        align-items:    stretch;
    }
    &__footer {
        padding: 10px 20px 20px;
    }
}



